.about {
  background-color: #134e5e;
  background-image: linear-gradient(339deg, rgba(39, 36, 73, 1) 0%, rgba(133, 164, 170, 1) 100%);
  color: $color-default-white;
  clip-path: polygon(0 0, 100% 0, 100% 90%, 0 100%);

  h2 {
    @include reset-style;

    font-size: 40px;
    text-align: center;
    margin-bottom: 45px;
  }
}

.about__wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.about__wrapper-family {
  display: grid;
  grid-template-columns: 300px 358px;
  justify-content: end;
  gap: 20px;
  margin-top: -200px;
}
