.contacts {
  background-image: linear-gradient(177deg,rgba(39, 36, 73, 1) 0%,rgba(133, 164, 170, 1) 100%);
  clip-path: polygon(0 15vh,100% 0,100% 100%,0 100%);
  color: $color-default-white;

  h2 {
    @include reset-style;

    font-size: 40px;
    line-height: 60px;
    text-align: center;
    margin-bottom: 70px;
  }
}

.contacts__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;

  a {
    font-size: 25px;
    line-height: 30px;
    color: $color-default-white;
    position: relative;
    border: 2px solid $color-default-white;
    border-image-slice: 1;
    transition: all 200ms ease;
    padding: 5px 10px;

    &:before {
      content:'';
      position: absolute;
      top: 0;
      left: 0;
      width: 0;
      height: 100%;
      background-color: $color-default-white;
      transition: all 200ms ease;
      z-index: -1;
    }

    @include hover {
      background-image: linear-gradient(135deg,rgba(39, 36, 73, 1),rgba(133, 164, 170, 1));
      -webkit-text-fill-color: transparent;
      -webkit-background-clip: text;
      background-clip: text;

      &:before {
        width: 100%;
      }
    }
  }
}
